.catalog-header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  justify-content: space-between;
  column-gap: 1rem;
}
.catalog-header__title {
  line-height: 88%;
  font-size: 2.57rem;
  font-weight: 400;
  margin-bottom: 48px;
  grid-column: 1/-1;
}

.catalog-header__btn {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: transparent;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 0.86rem;
  font-weight: 500;
  letter-spacing: 0.06em;
  line-height: 15px;

  &:hover {
    text-decoration: underline;
  }
}
.catalog-header__btn--filter {
  @media (min-width: $width-tablet-h) {
    display: none;
  }
}
.catalog-header__btn--sort-select {
  grid-column: -1/-2;
  display: grid;
  grid-template-columns: auto min-content;
  gap: 4px;
  align-items: center;

  &[aria-expanded="true"] {
    ~ .sort-list-values {
      opacity: 1;
      visibility: visible;
      right: 1rem;
      z-index: 99;

      @media (min-width: $width-tablet-h) {
        right: 2rem;
      }
    }
  }
}
